.wrapper {
  position: sticky;
  top: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
  padding: 10px 0;
  background: white;
  font-size: 18px;

  .search {

    margin-bottom: 20px;

    input {
      width: 300px;
      padding: 6px 12px;
      margin-right: 12px;
      border: none;
      border-radius: 6px;
      background: #9a9adb;
      color: white;
      font-size: 18px;

      &:focus-visible {
        outline: none;
        box-shadow: 0 0 5px -1px #000000;
      }
    }

    button {
      padding: 6px 12px;
      border: none;
      border-radius: 6px;
      background: #9a9adb;
      color: white;
      font-size: 18px;
      cursor: pointer;

      &:active {
        box-shadow: inset 0 0 5px -1px #000000;
        transform: scale(.95);
      }

      &:disabled {
        background: #c2c2fa;
        cursor: not-allowed;
      }

      &:disabled:active {
        background: #c2c2fa;
        box-shadow: none;
        transform: none;
      }
    }
  }
}

.sort {
  display: flex;
  align-items: center;
  margin-bottom: 20px;

  .checkboxContainer {
    display: flex;
    flex-direction: row-reverse;

    input[type='radio'] {
      display: none;
    }

    span {
      margin: 6px;
    }

    label {
      margin: 6px;
      padding: 6px 12px;
      cursor: pointer;
      border: 2px solid transparent;
      border-radius: 5px;
      user-select: none;
    }

    input[type='radio']:checked ~ label {
      border: 2px solid #9a9adb;
    }
  }
}

.sortSelect {
  display: none;

  span {
    margin-right: 20px;
  }

  select {
    width: 200px;
    padding: 6px 12px;
    border: 2px solid #9a9adb;
    border-radius: 6px;
    font-size: 14px;

    &:focus-visible {
      outline: none;
    }
  }

}

@media screen and (max-width: 550px) {
  .sort {

    .checkboxContainer {
      display: none;
    }

    .sortSelect {
      display: flex;
      align-items: center;
    }
  }
}

@media screen and (max-width: 450px) {
  .wrapper {
    .search {
      input {
        width: auto;
      }
    }
  }
}